<template>
  <div class="first1-list">
    <!-- <p style='width: 200px;height:100px;background: #000;color:#fff;font-size:24px;' @click='testGrowingIo'>testGrowingIo</p> -->
    <div class="c-box">
      <div class="container-left" v-if="show">淡泊以明誌</div>
    </div>
    <div class="container">
      <div class="baby" style="">
        <p @click="show=!show">{{ birthdy }}</p>
        <img :src="baby" alt="" v-show="show"/>
      </div>
      <ul class="person-data">
        <li></li>
        <li>
          <img width="150px" src="/images/phone.jpg" alt="" />
          <div class="face"></div>
        </li>
        <li></li>
      </ul>
      <div class="daily-list">
        <div class="search-box">
          <el-select v-model="year" size="small" style="width: 100px;margin-right: 10px;">
            <el-option
              v-for="item in everyDay"
              :key="item.year"
              :label="`${item.year}年`"
              :value="item.year"
            />
          </el-select>
          <el-select v-model="month" size="small" style="width: 100px;">
            <el-option
              v-for="item in monthOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </div>
        <p class="title">有道每日一句:</p>
        <ul>
          <li v-for="it in dailyList" :key="it.day">
            <p><span>日期：</span><span>{{year}}-{{addZero(month)}}-{{addZero(it.day)}}</span></p>
            <p><span>EN：</span><span>{{ it.en }}</span></p>
            <p><span>ZH：</span><span>{{ it.zh }}</span></p>
          </li>
        </ul>
      </div>
      <br />
      <div class="note-list">
        <p class="title">Aphorism</p>
        <ul>
          <li
            v-for="(item, index) in aphorism"
            :key="index"
          >
            {{ index + 1 + '. ' + item.label }}
          </li>
        </ul>
      </div>
      <div class="color-list">
        <ul>
          <li v-for="(item, index) in colorList" :key="index">
            <span>{{ item.name }}</span>
            <span>{{ item.color }}</span>
            <span>{{ item.hexColorValue }}</span>
            <span :style="{ background: item.color }"></span>
          </li>
        </ul>
      </div>
    </div>
    <div class="c-box">
      <div class="container-left" v-if="show">寧靜以致遠</div>
    </div>
  </div>
</template>

<script>
  import { everyDay, aphorism, colorList } from '../js/first'
  import baby from '/static/images/baby.jpg'
  export default {
    data() {
      const monthOptions = Array.from({length: 12}, (k, v) => {
        return {
          label: `${v+1}月`,
          value: v+1
        }
      })
      const year = new Date().getFullYear()
      const month = new Date().getMonth() + 1
      return {
        showBabyImg: false,
        baby,
        year,
        month,
        everyDay,
        monthOptions,
        aphorism,
        colorList,
        html: 'www.test9.zhixueyun.com',
        color: 'red',
        birthdy: '',
        show: false
      }
    },
    computed: {
      dailyList() {
        const {monthList=[]} = everyDay.find(it => it.year===this.year) || {}
        const {dayList=[]} = monthList.find(it => it.month===this.month) || {}
        return dayList
      }
    },
    created() {
      this.calcBirthDay()
    },
    methods: {
      testGrowingIo() {
        console.log('testGrowingIo')
      },
      calcBirthDay() {
        const vm = this
        setInterval(() => {
          const birthdy = '2020/08/12 11:24'
          const current = new Date().getTime() / 1000
          const old = new Date(birthdy).getTime() / 1000
          const differ = current - old
          const d = parseInt(differ / (3600 * 24))
          const hour = parseInt(differ / 3600) % 24
          const min = parseInt(differ / 60) % 60
          const s = parseInt(differ % 60)
          const day = d % 30
          const month = parseInt(d / 30) % 12
          const year = parseInt(d / 365)
          vm.birthdy = `${year}年${month}个月${day}天${hour}小时${min}分钟${s}秒`
        }, 1000)
      },
      addZero(v) {
        return v<10?`0${v}`:v
      }
    }
  }
</script>
